import React, { useState, useEffect, useMemo, useCallback } from 'react'
import { useTranslation } from 'react-i18next'
import { Link, Redirect } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { Row, Col, FormGroup, Button } from 'reactstrap'
import { Formik, Form, Field } from 'formik'

import logo from '../../scss/media/Daco_2105390.png'
import avatar from '../../scss/media/placeholder.7e85be59.svg'
import routes from '../constants/routes.constants'
import * as actions from '../actions/auth.actions'
import { api } from '../api'
import { isValidEmail, isValidPassword, isValidUsername, isValidPhone } from '../utils'

import Spinner from '../components/Spinner'
import Checkbox from '../components/Checkbox'
import InputPhone from '../components/InputPhone'
import Input from '../components/Input'

// eslint-disable-next-line react/prop-types
function SignUp({ location }) {
  const dispatch = useDispatch()
  const { t } = useTranslation('common')
  const inviterLoading = useSelector((state) => state.auth.loadings.inviter)
  const inviter = useSelector((state) => state.auth.inviter)
  const [signUpStatus, setSignUpStatus] = useState(null)
  const [serverError, setServerError] = useState(null)

  const referralName = useMemo(() => {
    let referral = ''
    if (location) {
      // eslint-disable-next-line react/prop-types
      const searchParams = new URLSearchParams(location.search)
      const ref = searchParams.get('ref')
      if (ref) {
        referral = ref
      }
    }

    return referral
  }, [location])

  const initialValues = useMemo(
    () => ({
      phone: '',
      first_name: '',
      last_name: '',
      username: '',
      email: '',
      password: '',
      repeatPassword: '',
      referral: referralName,
      acceptPrivacyPolicy: false,
    }),
    [referralName],
  )

  const getInviterByName = useCallback(
    (name) => {
      dispatch(actions.inviter(name))
    },
    [dispatch],
  )

  useEffect(() => {
    if (referralName) {
      getInviterByName(referralName)
    }
    return () => {
      dispatch(actions.clearInviter())
    }
  }, [dispatch, referralName, getInviterByName])

  const handleOnBlurReferralField = (event) => {
    const inviterName = event.target.value
    if (inviterName) {
      getInviterByName(inviterName)
    } else {
      dispatch(actions.clearInviter())
    }
  }

  const handleOnSubmit = ({ acceptPrivacyPolicy, repeatPassword, ...finalUserInfo }) => {
    setSignUpStatus('progress')
    setServerError(null)
    api
      .SignUp(finalUserInfo)
      .then(() => {
        setSignUpStatus('successful')
      })
      .catch((error) => {
        setServerError(error.message)
        setSignUpStatus('failed')
      })
  }

  // TODO: refactoring
  if (signUpStatus === 'successful') {
    // eslint-disable-next-line react/react-in-jsx-scope
    return <Redirect to={routes.SignIn} />
  }

  return (
    // eslint-disable-next-line react/react-in-jsx-scope
    <div className="auth__wrapper">
      {/* eslint-disable-next-line react/react-in-jsx-scope */}
      <div className="page">
        {/* eslint-disable-next-line react/react-in-jsx-scope */}
        <div className="auth__page">
          {/* eslint-disable-next-line react/react-in-jsx-scope */}
          <div className="auth__logo">
            {/* eslint-disable-next-line react/react-in-jsx-scope */}
            <Link to={routes.root}>
              {/* eslint-disable-next-line react/react-in-jsx-scope */}
              <img src={logo} alt="Stars logo" />
            </Link>
          </div>
          {/* eslint-disable-next-line react/react-in-jsx-scope */}
          <Formik
            initialValues={initialValues}
            validate={({
              phone,
              first_name,
              last_name,
              username,
              email,
              password,
              repeatPassword,
              acceptPrivacyPolicy,
            }) => {
              const errors = {}

              if (!first_name.trim()) {
                errors.first_name = t('Зополните это поле')
              }

              if (!last_name.trim()) {
                errors.last_name = t('Зополните это поле')
              }

              if (!isValidPhone(phone)) {
                errors.phone = t('Зополните это поле')
              }

              if (!isValidEmail(email)) {
                errors.email = t('Зополните это поле')
              }

              if (!isValidUsername(username)) {
                errors.username = t('Зополните это поле')
              }

              if (!isValidPassword(password)) {
                errors.password = t('Пароль должен состоять минимум из 8 символов')
              }

              if (repeatPassword !== password) {
                errors.repeatPassword = t('Пароли не совпадают')
              }

              if (!acceptPrivacyPolicy) {
                errors.acceptPrivacyPolicy = 'Required'
              }

              return errors
            }}
            onSubmit={handleOnSubmit}
          >
            {() => (
              // eslint-disable-next-line react/react-in-jsx-scope
              <Form className="auth__form">
                {/* eslint-disable-next-line react/react-in-jsx-scope */}
                <div className="auth__title">
                  {/* eslint-disable-next-line react/react-in-jsx-scope */}
                  <h2>{t('Регистрация в системме')}</h2>
                  {/* eslint-disable-next-line react/react-in-jsx-scope */}
                  {!inviter && <div>Скорее регистрируйся и начни зарабатывать уже сейчас!</div>}
                </div>
                {/* eslint-disable-next-line react/react-in-jsx-scope */}
                <Spinner size="sm" isLoading={inviterLoading}>
                  {inviter && (
                    // eslint-disable-next-line react/react-in-jsx-scope
                    <div className="inviter">
                      {/* eslint-disable-next-line react/react-in-jsx-scope */}
                      <div className="inviter__avatar">
                        {/* eslint-disable-next-line react/react-in-jsx-scope */}
                        <img
                          src={
                            inviter.avatar
                              ? `${process.env.REACT_APP_BASE_URL}${inviter.avatar}`
                              : avatar
                          }
                          alt={`${inviter.firstName} ${inviter.lastName}`}
                        />
                      </div>
                      {/* eslint-disable-next-line react/react-in-jsx-scope */}
                      <div className="inviter__info">
                        {/* eslint-disable-next-line react/react-in-jsx-scope */}
                        <div className="inviter__name">{`${inviter.firstName} ${inviter.lastName}`}</div>
                        {/* eslint-disable-next-line react/react-in-jsx-scope */}
                        <div>Скорее регистрируйся и начни зарабатывать уже сейчас!</div>
                      </div>
                    </div>
                  )}
                </Spinner>
                {/* eslint-disable-next-line react/react-in-jsx-scope */}
                <FormGroup>
                  {/* eslint-disable-next-line react/react-in-jsx-scope */}
                  <Field
                    type="text"
                    name="referral"
                    placeholder={t('Логин пригласителя')}
                    onBlur={handleOnBlurReferralField}
                    component={Input}
                  />
                </FormGroup>
                {/* eslint-disable-next-line react/react-in-jsx-scope */}
                <Row>
                  {/* eslint-disable-next-line react/react-in-jsx-scope */}
                  <Col lg={6}>
                    {/* eslint-disable-next-line react/react-in-jsx-scope */}
                    <FormGroup>
                      {/* eslint-disable-next-line react/react-in-jsx-scope */}
                      <Field
                        type="text"
                        name="first_name"
                        placeholder={t('Введите Имя')}
                        component={Input}
                      />
                    </FormGroup>
                  </Col>
                  {/* eslint-disable-next-line react/react-in-jsx-scope */}
                  <Col lg={6}>
                    {/* eslint-disable-next-line react/react-in-jsx-scope */}
                    <FormGroup>
                      {/* eslint-disable-next-line react/react-in-jsx-scope */}
                      <Field
                        type="text"
                        name="last_name"
                        placeholder={t('Введите Фимилию')}
                        component={Input}
                      />
                    </FormGroup>
                  </Col>
                </Row>
                {/* eslint-disable-next-line react/react-in-jsx-scope */}
                <FormGroup>
                  {/* eslint-disable-next-line react/react-in-jsx-scope */}
                  <Field
                    name="phone"
                    placeholder={t('Номер телеона в формате +99999999999')}
                    component={InputPhone}
                  />
                </FormGroup>
                {/* eslint-disable-next-line react/react-in-jsx-scope */}
                <FormGroup>
                  {/* eslint-disable-next-line react/react-in-jsx-scope */}
                  <Field
                    type="text"
                    name="email"
                    placeholder={t('Введите почтовый ящик')}
                    component={Input}
                  />
                </FormGroup>
                {/* eslint-disable-next-line react/react-in-jsx-scope */}
                <FormGroup>
                  {/* eslint-disable-next-line react/react-in-jsx-scope */}
                  <Field
                    type="text"
                    name="username"
                    placeholder={t('Придумайте логин')}
                    component={Input}
                  />
                </FormGroup>
                {/* eslint-disable-next-line react/react-in-jsx-scope */}
                <Row>
                  {/* eslint-disable-next-line react/react-in-jsx-scope */}
                  <Col lg={6}>
                    {/* eslint-disable-next-line react/react-in-jsx-scope */}
                    <FormGroup>
                      {/* eslint-disable-next-line react/react-in-jsx-scope */}
                      <Field
                        type="password"
                        name="password"
                        placeholder={t('Придумайте пароль')}
                        component={Input}
                      />
                    </FormGroup>
                  </Col>
                  {/* eslint-disable-next-line react/react-in-jsx-scope */}
                  <Col lg={6}>
                    {/* eslint-disable-next-line react/react-in-jsx-scope */}
                    <FormGroup>
                      {/* eslint-disable-next-line react/react-in-jsx-scope */}
                      <Field
                        type="password"
                        name="repeatPassword"
                        placeholder={t('Повторите пароль')}
                        component={Input}
                      />
                    </FormGroup>
                  </Col>
                </Row>
                {/* eslint-disable-next-line react/react-in-jsx-scope */}
                <FormGroup className="mt-4 mb-5">
                  {/* eslint-disable-next-line react/react-in-jsx-scope */}
                  <Field
                    type="checkbox"
                    name="acceptPrivacyPolicy"
                    label={t('Регистрируясь я принимаю условия системмы')}
                    component={Checkbox}
                  />
                </FormGroup>
                {/* eslint-disable-next-line react/react-in-jsx-scope */}
                <Button
                  type="submit"
                  color="primary"
                  disabled={signUpStatus === 'progress'}
                  size="lg"
                  block
                >
                  {t('Зарегистрироватся')}
                </Button>
                {/* eslint-disable-next-line react/react-in-jsx-scope */}
                {serverError && <div className="auth__error">{serverError}</div>}
              </Form>
            )}
          </Formik>
          {/* eslint-disable-next-line react/react-in-jsx-scope */}
          <div className="auth__footer">
            {t('У Вас уже усть аккаунт?')} {/* eslint-disable-next-line react/react-in-jsx-scope */}
            <Link to={routes.SignIn}>{t('Смело входите')}</Link>
          </div>
        </div>
      </div>
    </div>
  )
}

export default SignUp
