import { useEffect } from 'react'
import { useDispatch } from 'react-redux'
import { Formik, Form, Field } from 'formik'
import styles from './Avatar.module.scss'
import avatar from '../../../scss/media/placeholder.7e85be59.svg'
import { api } from '../../api'
import * as actions from '../../actions/app.actions'
import { isValidImageType } from '../../utils'

const AutoSubmit = ({ initialValues, values, submitForm }) => {
  useEffect(() => {
    if (initialValues !== values) {
      submitForm()
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [values])
  return null
}

// eslint-disable-next-line react/prop-types
export default function Avatar({ url, className }) {
  const dispatch = useDispatch()

  const submitAvatarForm = ({ avatar }) => {
    api
      .updateAvatar(avatar)
      .then(() => {
        api
          .getUserInfo()
          .then((response) => {
            dispatch(actions.userInfoSuccess(response))
          })
          .catch(() => {})
      })
      .catch(() => {})
  }

  return (
    // eslint-disable-next-line react/react-in-jsx-scope
    <div className={`${styles.Avatar} ${className}`}>
      {/* eslint-disable-next-line react/react-in-jsx-scope */}
      <Formik
        initialValues={{
          avatar: null,
        }}
        validate={({ avatar }) => {
          const errors = {}

          if (avatar.size / 1024 / 1024 >= 10) {
            errors.avatar = 'Размер изображения превышает 10Mb'
          }

          if (!isValidImageType(avatar.type)) {
            errors.avatar = 'Неверный формат изображения'
          }

          return errors
        }}
        onSubmit={submitAvatarForm}
      >
        {(props) => (
          // eslint-disable-next-line react/react-in-jsx-scope
          <Form>
            {/* eslint-disable-next-line react/react-in-jsx-scope */}
            <Field>
              {({ form }) => (
                // eslint-disable-next-line react/react-in-jsx-scope
                <div className={styles.inputWrapper}>
                  {/* eslint-disable-next-line react/react-in-jsx-scope */}
                  <input
                    type="file"
                    accept="image/png, image/jpeg, image/jpg"
                    onChange={({ target: { files } }) => {
                      if (files && files[0]) {
                        const image = files[0]
                        form.setFieldValue('avatar', image)
                      }
                    }}
                  />
                  {/* eslint-disable-next-line react/react-in-jsx-scope */}
                  <img src={url || avatar} alt="аватар пользователя" />
                </div>
              )}
            </Field>
            {/* eslint-disable-next-line react/prop-types,react/react-in-jsx-scope */}
            {props.errors && <p className={styles.errorMessage}>{props.errors.avatar}</p>}
            {/* eslint-disable-next-line react/react-in-jsx-scope */}
            <AutoSubmit {...props} />
          </Form>
        )}
      </Formik>
      {/* eslint-disable-next-line react/react-in-jsx-scope */}
      <span className={styles.circle}> </span>
    </div>
  )
}
