import React, { useEffect } from 'react'
import Header from './pages/public/Home/Header'
import Banner from './pages/public/Home/Banner'
import Authentication from './pages/public/Home/Authentication'
import Footer from './pages/public/Home/Footer'
import AOS from 'aos'
import 'aos/dist/aos.css'

function Home() {
  useEffect(() => {
    AOS.init()
    AOS.refresh()
  }, [])

  return (
    <div className="page">
      <Header />
      <Banner />
      <Authentication />
      <Footer />
    </div>
  )
}

export default Home
