import { useState } from 'react'
import { useTranslation } from 'react-i18next'
import { Link } from 'react-router-dom'
import { Formik, Form, Field } from 'formik'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import styles from './ResetPassword.module.scss'
import routes from '../../constants/routes.constants'
import logo from '../../../scss/media/Daco_2105390.png'
import { isValidEmail } from '../../utils'
import { api } from '../../api'

import Input from '../../components/OldInput'
import Button from '../../components/OldButton'

export default function ResetPassword() {
  const [resetPasswordStatus, setResetPasswordStatus] = useState(null)
  const { t } = useTranslation('common')

  const submitResetPasswordForm = ({ email }) => {
    setResetPasswordStatus('pending')
    api
      .resetPassword(email)
      .then((response) => {
        setResetPasswordStatus('successful')
      })
      .catch(() => {
        setResetPasswordStatus('failed')
      })
  }

  return (
    // eslint-disable-next-line react/react-in-jsx-scope
    <div className={styles.ResetPasswordWrapper}>
      {/* eslint-disable-next-line react/react-in-jsx-scope */}
      <div className="page">
        {/* eslint-disable-next-line react/react-in-jsx-scope */}
        <div className={styles.ResetPassword}>
          {/* eslint-disable-next-line react/react-in-jsx-scope */}
          <div className={styles.logo}>
            {/* eslint-disable-next-line react/react-in-jsx-scope */}
            <Link to={routes.root} className="d-block">
              {/* eslint-disable-next-line react/react-in-jsx-scope */}
              <img src={logo} alt="Stars logo" />
            </Link>
          </div>
          {/* eslint-disable-next-line react/react-in-jsx-scope */}
          <Container>
            {/* eslint-disable-next-line react/react-in-jsx-scope */}
            <Row>
              {/* eslint-disable-next-line react/react-in-jsx-scope */}
              <Col xl={{ span: 8, offset: 2 }}>
                {/* eslint-disable-next-line react/react-in-jsx-scope */}
                <Formik
                  initialValues={{ email: '' }}
                  validate={({ email }) => {
                    const errors = {}

                    if (!isValidEmail(email)) {
                      errors.email = t('Такого Email Не существует')
                    }

                    return errors
                  }}
                  onSubmit={submitResetPasswordForm}
                >
                  {() => (
                    // eslint-disable-next-line react/react-in-jsx-scope
                    <Form className={styles.form}>
                      {/* eslint-disable-next-line react/react-in-jsx-scope */}
                      <h2 className={styles.mainTitle}>{t('Востановление пароля')}</h2>
                      {/* eslint-disable-next-line react/react-in-jsx-scope */}
                      <Field
                        className={styles.field}
                        type="email"
                        name="email"
                        placeholder={t('Введите Email с которым регистрировались')}
                        component={Input}
                      />
                      {/* eslint-disable-next-line react/react-in-jsx-scope */}
                      <Button
                        className={styles.submitButton}
                        color="perrywinkle"
                        size="medium"
                        type="submit"
                        disabled={resetPasswordStatus === 'pending'}
                      >
                        {t('Востановить пароль')}
                      </Button>
                      {resetPasswordStatus === 'successful' && (
                        // eslint-disable-next-line react/react-in-jsx-scope
                        <p className="mt-3 text-center">{t('Пароль отправлен вам на почту')}</p>
                      )}
                    </Form>
                  )}
                </Formik>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    </div>
  )
}
